.breadcrumb {
  background: none;
  padding: 0;
  font-size: 13px;
  margin-bottom: 15px;
  ul {
      display: flex;
      align-items: center;
      margin-bottom: 0;
      li {
          position: relative;
          padding-right: 17px;
          margin-right: 8px;
          color: rgba($black, 0.6);
          &.active {
              color: $primary;
          }
          &:after {
              font-family: $primaryIcons;
              content: "\ecee";
              @include position(absolute, 0, -2px, 0, null);
              color: rgba($black, 0.6);
          }
          &:last-child {
              margin-right: 0;
              padding-right: 0;
              &:after {
                  display: none;
              }
          }
      }
  }
  a {
      color: rgba($black, 0.6);
      &:hover {
          color: rgba($black, 0.9);
      }
      &.active {
          color: $primary;
          &:hover {
              color: $primary;
          }
      }
  }
}