.btn {
  border-radius: $border-radius;
  padding: 12px 20px;
  transition: all 0.5s;
  border-radius: 4px;
  min-width: 100px;
  .btn-loader {
      display: none;
      @include position(absolute, 0, 0, 0, 0);
      align-items: center;
      justify-content: center;
  }
  &:focus {
      box-shadow: none !important;
  }
  &.btn-lg {
      padding: 10px 22px !important;
      font-size: 18px !important;
  }

  &.btn-sm {
      padding: 8px 16px !important;
      font-size: 13px !important;
  }

  &.btn-xs {
      padding: 5px 10px !important;
      font-size: 10px !important;
  }
}

@each $key, $val in $theme-colors {
  .btn-#{$key} {
      background: $val;
      border-color: $val;
      &:hover, &:focus, &:active{
          background: darken($val, 8) !important;
          border-color: darken($val, 8) !important;
      }
  }
}

.browse-all {
  padding: 12px 30px;
}

.btn-primary-gred {
  background: $gred-btn-primary;
  border-color: transparent;
  background-size: 200% auto;
  box-shadow: none;
  color: $white;
  &:hover,
  &:focus,
  &.focus,
  &:active,
  &.active {
      background-position: right center;
      color: $white;
  }
}

.btn-white {
  background: $white;
  color: $primary;
  &:hover {
      background: $white;
      color: $primary;
  }
}

.btn-outline-white {
  background: transparent;
  color: $white;
  border-color: $white;
  &:hover {
      background: $white;
      color: $primary;
  }
}

.btn-outline-primary {
  color: $primary;
  border-color: $border-color;
  &:hover {
      background: $primary;
      color: $white;
  }
}