
.pagination {
  justify-content: center;
  .page-item {
      margin-right: 5px;
      &:last-child {
          margin-right: 0;
          .page-link {
              border-radius: 100%;
          }
      }
      .page-link {
          border: none;
          width: 40px;
          height: 40px;
          display: flex;
          align-items: center;
          justify-content: center;
          border-radius: 100%;
          color: $default-color;
          &:hover {
              background: $light;
          }
      }
      &.active {
          a, span {
              background: $primary;
              color: $white;
              &:hover {
                  background: $primary;
                  color: $white;
              }
          }
      }
      &.disabled {
          opacity: 0.5;
      }
  }
}